.mainImg {
  position: relative;
  width: 100%;
}

.mainImg .mainImg__inner {
  width: 1120px;
  height: 580px;
  margin: 0 auto;
}
.mainImg .mainImg__inner .mainImg__nav {
  width: 61px;
  height: auto;
  position: absolute;
  z-index: 2;
}
.mainImg .mainImg__inner .mainImg__nav ul {
}

.mainImg .mainImg__inner .mainImg__nav > ul > li {
  width: 61px;
  height: 71px;
  text-align: center;
  padding-top: 20px;
  box-sizing: border-box;
}

.mainImg .mainImg__inner .mainImg__nav ul li span {
  line-height: 10px;
  font-size: 30px;
}

.mainImg .mainImg__inner .mainImg__nav ul li span p {
  width: 100%;

  top: 30px;
  left: 0px;
  font-size: 11px;
}

.mainImg .mainImg__left {
  position: absolute;
  top: -100px;
  left: 0;
  width: 60%;
  height: 650px;
  background-color: #f7f7f7;
  opacity: 0.5;
  z-index: -1;
}
.mainImg .mainImg__right {
  position: absolute;
  top: 0;
  left: 50%;
  width: 1215px;
  height: 580px;
  margin-left: -415px;
  background-image: url(./component/images/mainImg_bg.png);
  background-repeat: no-repeat;
}

/* content */
.contents {
  width: 100%;
}

.contents > .product1 {
  width: 1120px;
  height: 610px;
  margin: auto;
  padding: 100px 0;
  box-sizing: border-box;
  position: relative;
}

.contents > .product1 > .product1__img1 {
  background-image: url('./component/images/bg_main_circle_pink.png');
  width: 112px;
  height: 112px;
  position: absolute;
  z-index: 0;
  top: 40px;
  left: -90px;
}
.contents > .product1 > .product1__img2 {
  background-image: url('./component/images/bg_main_circle_green.png');
  width: 217px;
  height: 217px;
  position: absolute;
  z-index: 0;
  top: 150px;
  left: 120px;
}
.contents > .product1 > .product1__img3 {
  background-image: url('./component/images/bg_main_circle_line.png');
  width: 360px;
  height: 252px;
  position: absolute;
  left: -298px;
  bottom: 0;
}

.contents > .product1 > ul {
  position: absolute;
  z-index: 1;
  display: flex;
}

.contents > .product1 > ul > .product1__title > h2 {
  font-size: 32px;
  margin-bottom: 18px;
}

.contents > .product1 > ul > .product1__title > p {
  font-size: 14px;
  color: #7e7e7e;
  line-height: 22px;
}

.contents > .product1 > ul > .product1__title .product1__btn > button {
  background-color: rgba(255, 255, 255, 1);
  border: #fff;
  font-weight: bold;
  font-size: 25px;
  margin: 100px 20px 0 0;
}
.prod__boxs {
  position: absolute;
  left: 280px;
  display: flex;
}

.prod__boxs > div .product1__prod {
  width: 272px;
  text-align: center;
  margin-right: 10px;
  color: #333;
  border: 1px solid #ededed;
  background-color: #fff;

  box-sizing: border-box;
}
.prod__boxs > div .product1__prod > img {
  margin: 30px 0;
}

.prod__boxs > div .product1__prod > h4 {
  font-size: 17px;
  padding: 11px 20px;
  line-height: 24px;
  box-sizing: border-box;
  font-weight: 400;
}

.prod__boxs > div .product1__prod > p {
  padding: 7px 20px 24px;
  box-sizing: border-box;
  text-align: right;
  font-weight: 400;
}

.prod__boxs > div .product1__prod > p span {
  font-size: 24px;
}

/* product02 */

.product2 {
  width: 100%;
  height: auto;
  background-color: #f3f5f7;
}
.product2 .product2__inner {
  width: 1120px;
  margin: 0 auto;
  padding: 100px 0;
  box-sizing: border-box;
}
.product2 .product2__inner > ul .product2__title {
  font-size: 32px;
  margin-bottom: 28px;
}
.product2 .product2__inner > ul .product2__title h3 {
}
.product2 .product2__inner > ul .product2__title h3 {
}

.product2 .product2__inner > ul .product2__nav {
}
.product2 .product2__inner > ul .product2__nav ul {
  display: flex;
}
.product2 .product2__inner > ul .product2__nav ul li {
  font-size: 16px;
  margin-right: 25px;
  color: #999999;
  cursor: pointer;
}

.product2 .product2__inner > ul > .product2__product {
}

.product2 .product2__inner > ul > .product2__product ul {
  width: 1120px;
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
  margin-top: 20px;
}
.product2 .product2__inner > ul > .product2__product ul li {
  width: 270px;
  height: 410px;
  background-color: #fff;
  border: 1px solid #ededed;
  margin: 0 10px 10px 0;
  box-sizing: border-box;
}

/* product3 */
.product3 {
  width: 100%;
  padding: 100px 0;
  box-sizing: border-box;
  position: relative;
}

.product3 .inner {
  width: 1120px;
  margin: 0 auto;
}
.product3 .product3__bgpink {
  background-image: url('./component/images/section03_bg_pink.png');
  width: 112px;
  height: 112px;
  position: absolute;
  top: 40px;
  left: 300px;
  z-index: -1;
}
.product3 .product3__bgblue {
  background-image: url('./component/images/section03_bg_blue.png');

  width: 217px;
  height: 217px;
  position: absolute;
  top: 100px;
  left: 550px;
  z-index: -2;
}
.product3 .product3_title {
  width: 252px;
  float: left;
  margin-right: 28px;
  z-index: 10;
}
.product3 .product3_title h3 {
  font-size: 32px;
  font-weight: 600;
  margin-bottom: 28px;
}
.product3 .product3_title ul {
  float: left;
}
.product3 .product3_title ul > li {
  padding: 17px 20px 17px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  font-weight: 400;
  color: #7e7e7e;
}

.product3 .product3_title ul > li:first-child {
  font-weight: 400;
  box-shadow: 0 3px 5px 2px #eeeff1;
  border-bottom: 1px solid #fff;
  border-left: 4px solid red;
  color: #000;
}

.product3 .inner .product3__prodcut {
  display: flex;
  flex-wrap: wrap;
}
.product3 .inner .product3__prodcut > li {
  width: 268px;
  height: 410px;
  background-color: #fff;
  margin-right: 10px;
  margin-bottom: 10px;
  border: 1px solid #ededed;
}

/* product4 */
.product4 {
  width: 100%;
  background-color: #f3f5f7;
  overflow: hidden;
  padding: 100px 0;
  box-sizing: border-box;
}

.product4 .product4__inner {
  width: 1120px;

  margin: 0 auto 28px;
  box-sizing: border-box;
}

.product4 .product4__inner > h3 {
  font-size: 32px;
  font-weight: 600;
}

.product4__scroll {
  width: 3540px;
}

.product4__scroll > ul {
  display: flex;
}

.product4__scroll > ul li {
  width: 230px;
  height: 309px;
  background-color: #ccc;
  margin: 30px 0 10px 25px;
}
.product4__scroll > ul li:first-child {
  margin: 30px 0 10px 0;
}

.product4__scrollbar {
  width: 1120px;
  margin: 35px auto;
}

.product4__scrollbar .scrollbar__full {
  width: 100%;
  height: 2px;
  background-color: rgba(0, 0, 0, 0.15);

  position: relative;
}

.product4__scrollbar .scrollbar__full .scrollbar__innerblack {
  width: 80px;
  height: 4px;
  border-radius: 10px;
  background-color: #000;
  position: absolute;
  top: -1px;
}

/* section05 */

.product5 {
  width: 100%;
  padding: 100px 0;
  box-sizing: border-box;
}

.product5__inner {
  width: 1120px;
  margin: 0 auto;
}

.product5__inner > h3 {
  font-size: 32px;
  font-weight: 600;
}

.product5__product {
  display: flex;
  justify-content: left;
}

.product5__product li {
  width: 357px;
  height: 486px;
  background-color: #7e7e7e;
  margin: 28px 20px 0 0;
}

.product5__banner {
  width: 100%;
  margin-top: 100px;
  display: flex;
  justify-content: space-between;
}

.product5__banner .banner__1 {
  background-image: url('./component/images/section05_ban1.png');
  width: 550px;
  height: 187px;
}
.product5__banner .banner__2 {
  background-image: url('./component/images/section5_ban2.png');
  width: 550px;
  height: 187px;
}
