.bg_left {
  left: -1060px;
  width: 100%;
  height: 60px;
  background-color: #ec2f2f;
  position: absolute;
}
.headerIn {
  position: relative;
  margin: 0 auto;
  height: 60px;
  width: 1120px;
  display: flex;
  justify-content: space-between;
}

.subnav {
  width: 1120px;
  margin: 0 auto;
  height: 35px;
}

.subnav > ul {
  display: flex;
  justify-content: flex-end;
}
.subnav > ul li:nth-child(1),
.subnav > ul li:nth-child(2) {
  margin-right: 4px;
  line-height: 35px;
  font-size: 11px;
}
.subnav > ul li:nth-child(3),
.subnav > ul li:nth-child(4) {
  margin-right: 4px;
  line-height: 35px;
  font-size: 14px;
}

.subnav > ul li {
  margin-right: 4px;
  font-size: 13px;
}
.subnav_icon {
  margin-left: 2px;
  font-size: 20px;
  vertical-align: text-bottom;
}

.subnav > ul li > span {
  color: #ec2f2f;
  font-size: 14px;
}
.subnav > ul li:nth-child(3):before,
.subnav > ul li:nth-child(3)::after {
  content: '|';
  margin-left: 4px;
  margin-right: 4px;
}

.subnav > ul li:last-child {
  margin-right: 0;
}

.logo_seciton {
  display: flex;
  align-items: flex-end;
}
.headerIn__button {
  background-image: url(./component/images/full_down_menu1.png);
  width: 60px;
  height: 60px;
  border: 0;
  background-color: #ec2f2f;
  cursor: pointer;
  z-index: 2;
}

.logo {
  display: block;
  line-height: 100px;
  background-image: url(./component/images/logo.png);
  background-repeat: no-repeat;
  width: 87px;
  height: 35px;
  margin-left: 40px;
  padding-bottom: 13px;
}

.sub_icon {
  margin-top: 10px;
  width: 208px;
  height: 35px;
  float: right;
}

.sub_icon ul {
  display: flex;
  justify-content: space-between;
  line-height: 35px;
}

.sub_icon ul > li {
  cursor: pointer;
  font-size: 29px;
  color: #111;
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}
