.inner {
  width: 1120px;
  height: 261px;
  margin: 0 auto;
  position: relative;
}

.inner .notice_section {
  display: flex;
  justify-content: space-between;
}

.inner .notice_section > li {
  padding: 65px 0 64px;
  width: 259px;
}

.inner ul li h2 {
  font-size: 24px;
  margin-bottom: 24px;
  font-weight: 400;
}

.inner ul li p {
  color: #7e7e7e;
  margin-bottom: 24px;
  font-size: 14px;
}

.inner ul li span {
  font-size: 14px;
  font-weight: 400;
}

.inner ul li span .RightIcon {
  margin-left: 5px;
  vertical-align: text-top;
  font-size: 15px;
}

.inner ul li:nth-child(3) > span {
  color: #444;
  font-size: 30px;
  font-weight: 600;
}
.inner > .notice_section .QnaSection > ul {
  display: flex;
  justify-content: center;

  text-align: center;
  width: 261px;
  height: 131px;
}
.inner > .notice_section .QnaSection > ul li {
  margin-right: 30px;
  margin-top: 20px;
}

.inner > .notice_section .QnaSection > p {
  color: #000;
  font-size: 18px;
  font-weight: 600;
}

.inner .QnaSection .QnaSection_Icon {
  font-size: 60px;
  margin-bottom: 30px;
}

/* footer menu */

.fMenu {
  width: 100%;
  height: 55px;
  border-top: 1px solid rgb(182, 182, 182);
  border-bottom: 1px solid rgb(182, 182, 182);
}

.fMenu .bi_section {
  width: 1120px;

  margin: 0 auto;
  display: flex;
}

.fMenu .bi_section > li {
  line-height: 55px;
  font-size: 14px;
  color: #444;
  font-weight: 400;
}

.fMenu .bi_section > li:nth-child(3) {
  font-weight: 600;
}

.fMenu .bi_section > li::after {
  margin: 0 10px;
  content: '|';
}

.fMenu .bi_section > li:last-child:after {
  margin: 0;
  content: '';
}

.fMenu .bi_section > li:last-child {
  margin: 0;
}

/* footer adress */
.fAddress {
  width: 100%;
  height: 245px;
}

.fAddress .FooterS__left > h4 {
  background-image: url(./component/images/f_logo.png);
  width: 87px;
  height: 35px;
}

.fAddress .FooterSection {
  padding: 30px;
  width: 1120px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
/* 860px 54px */

.FooterS__right {
  width: 860px;
  height: 54px;
}

.FooterS__right div > span {
  margin-right: 10px;
  font-size: 14px;
  font-weight: 400;
}

.FooterS__right div > span::after {
  content: '|';
  padding-left: 10px;
}

.FooterS__right div > span:last-child::after {
  content: '';
}

.FooterS__left {
  width: 260px;
  height: 188px;
}

.FooterS__left > .FooterS__btn {
  display: flex;
  margin-top: 30px;
}

.FooterS__left > .FooterS__btn > div > h4 {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #ddd;
  width: 121px;
  height: 31px;
  color: #444;
  font-size: 11px;
  margin-right: 10px;
  font-weight: 400;
  text-align: left;
}

.FooterS__left > .FooterS__btn > button {
  width: 50px;
  height: 31px;
  background-color: #999999;
  border: 0;
  color: #fff;
}
